<template>
  <div class="maxwin">
    <div style="margin-top:10px;margin-bottom: 10px;">
      <img src="../../assets/bottom01.png" width="100%">
    </div>
    <div v-for="(item,index) in listData.data">
      <div class="orderbox" :style="absbackimg">
        <div class="abstitle" :style="abstitle">
          <span>{{item.name}}</span>
          <br>
          <span>:{{item.order_sn}}</span>
          <br>
          <span>:{{item.create_time}}</span>
        </div>
        <div class="absimg" :style="absimg">
             <swiper :options="swiperOption">
              <swiper-slide  class="swiper-container" style="background:none;text-align: center;" v-for="goods in item.products" :key="goods.id">
                <div > {{goods.product_name}}X{{goods.product_nums}} </div>
              </swiper-slide>
              
            </swiper>
        </div>
        <div class="absprice" :style="absprice">
          <span v-if="item.pay_status == 1"></span>
          <span v-else="item.pay_status == 2"></span>
        </div>
        <router-link :to="{name:'Order',params:{id:item.order_id}}" tag='div' class="absstate" :style="absstate">
          <span></span>
        </router-link>
        <div class="absinfo" :style="absinfo">
          <span>{{item.total_amount}}</span>
        </div>
      </div>
      <div style="margin-top:10px;margin-bottom: 10px;">
        <img src="../../assets/bottom01.png" width="100%">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "GeneralOrder",
  data() {
    return {
      absbackimg: "",
      abstitle: "",
      absprice: "",
      absstate: "",
      absinfo: "",
      absimg: "",
      listData: "",
      //swiper start
      swiperOption: {
        autoplay: {
          disableOnInteraction: false, //
          delay: 5000 //
        }, //
        speed: 2000, //sliderms
        loop: false, //
        grabCursor: true, //trueSwiper
        // setWrapperSize: true, //Swiperflexbox(display: flex)Wrapperslidesflexbox
        autoHeight: true, //truewrappercontainerslide
        scrollbar: ".swiper-scrollbar",
        mousewheelControl: true, //trueslide
        observeParents: true, //swiper/swiperswiper
        pagination: {
          el: ".swiper-pagination",
           //type : 'progressbar', //
          clickable: true //Swiper
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      }
      //swiper end
    };
  },
  computed: {
    //
  },
  methods: {
    //
    getOrder: function() {
      this.$axios({
        method: "post",
        url: this.HOSTS + "/order/list",
        data: {}
      })
        .then(res => {
          if (res.data.code == 0) {
            this.listData = res.data.result;
          }
          console.log(res);
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  watch: {
    //
  },
  mounted: function() {
    //
    this.getOrder();
    let winBox  = window.innerWidth;
    if (winBox>500){
      winBox=500
    }
    this.absbackimg =
      this.absbackimg + "height:" + winBox * 0.7366 + "px;";
    this.abstitle =
      this.abstitle + "height:" + winBox * 0.144 + "px;";
    this.absprice =
      this.absprice + "height:" + winBox * 0.055 + "px;";
    this.absstate =
      this.absstate + "height:" + winBox * 0.055 + "px;";
    this.absimg = this.absimg + "height:" + winBox * 0.256 + "px;";
    this.absinfo = this.absinfo + "height:" + winBox * 0.08 + "px;";

    this.abstitle =
      this.abstitle +
      "line-height:" +
      ((winBox * 0.144) / 3).toFixed(0) +
      "px;";
    this.absprice =
      this.absprice +
      "line-height:" +
      (winBox * 0.055).toFixed(0) +
      "px;";
    this.absstate =
      this.absstate +
      "line-height:" +
      (winBox * 0.055).toFixed(0) +
      "px;";
    this.absinfo =
      this.absinfo +
      "line-height:" +
      (winBox * 0.08).toFixed(0) +
      "px;";

    // 		this.abstitle	=	this.abstitle	+ 'font-size:' + (winBox * 0.144/3*0.4).toFixed(0) + 'px;'
    // 		this.absprice	=	this.absprice	+ 'font-size:' + (winBox * 0.055*0.4).toFixed(0) + 'px;'
    // 		this.absstate	=	this.absstate	+ 'font-size:' + (winBox * 0.055*0.4).toFixed(0) + 'px;'
    // 		this.absinfo	=	this.absinfo	+ 'font-size:' + (winBox * 0.08*0.4).toFixed(0) + 'px;'
  }
};
</script>

<style scoped>
.swiper-container {
  width: 100%;
  height: 100%;
}
.orderbox {
  width: 100%;
  position: relative;
  background-image: url(../../assets/order/back01.png);
  background-size: 100%;
  background-repeat: no-repeat;
  color: #15f5ff;
  text-align: center;
}
.abstitle {
  position: absolute;
  top: 2.17%;
  left: 25%;
  /* border: 1px solid #0889B3; */
  width: 49%;
  /* height:54px; */
}
.absprice {
  position: absolute;
  bottom: 25%;
  left: 15%;
  /* border: 1px solid #0889B3; */
  width: 25%;
  /* height: 22px; */
}
.absstate {
  position: absolute;
  bottom: 25%;
  left: 61%;
  /* border: 1px solid #0889B3; */
  width: 25%;
  /* height: 22px; */
}
.absimg {
  position: absolute;
  top: 29.69%;
  left: 24%;
  border: 1px solid #0889b3;
  border-radius: 10px;
  width: 53%;
  /* height:96px; */
}
.absinfo {
  position: absolute;
  top: 85.08%;
  left: 29%;
  /* border: 1px solid #0889B3; */
  width: 41%;
  /* height:30px; */
}
</style>



